.header{
    display: flex ;
    justify-content: space-between;
    border: 1px solid black;
}
.logo {
 width: 100px;
}

.nav-items > ul {
    font-size: 24px;
    display: flex;
    list-style-type: none;
}

.nav-items > ul > li {
 padding: 10px;
 margin: 10px;   
}
 .res-container{
    display: flex;
    flex-wrap: wrap;
 }
.res-card {
    margin: 5px;
    padding: 5px ;
    width: 200px;
    height: 300px;
    flex-direction: column;
    justify-content: space-between; /* Spaces content evenly */
    align-items: center; /* Centers the content horizontally */
    
}
.res-card:hover{
    cursor:pointer;
    border: 1px solid black;
}
.search{
    padding: 10px;
}
.res-logo{
    width:100%;
    height: 140px; /* Adjust the height to fit the overall card */
    object-fit: cover;
}